aside {
  padding-top: 1rem;
}
aside div {
  background-color: var(--item-back);
  border: 1px solid var(--item-border);
  border-radius: 6px;
  padding: 0.75rem;
  margin-bottom: 1rem;
  transition: all 0.2s;
  margin-inline: 1rem;
}
aside div:hover {
  background-color: rgba(var(--accentRGB), 0.2);
  transform: translateY(-2px);
}
aside div h3 {
  margin: 0 0 0.5rem 0;
  color: var(--txt);
}
aside div p {
  margin: 0 0 0.5rem 0;
  color: var(--txt);
  font-size: 0.9rem;
}
aside div a {
  color: var(--accent);
  text-decoration: none;
  font-weight: bold;
}
aside div a:hover {
  text-decoration: underline;
}
aside div img {
  max-width: 100%;
  border-radius: 4px;
  margin-top: 0.5rem;
}
aside div details {
  margin-top: 5px;
}
aside div details summary {
  cursor: pointer;
}
aside div ul,
aside div ol {
  margin-left: 1rem;
}